// import { url } from 'inspector'
export default {
    data() {
        return {
            show:null
        }
    },
    created(){
this.show=this.isShow
    },
        props: {
            isShow: {
                type: Boolean
            }
        },
        watch:{
isShow(val){
this.show=val
}
        },
    methods: {
        close_card() {
            this.$emit("closeCard",false)
        },
        goCarWash() {
          console.log(1)
          uni.switchTab({
            url: "/pages/car_wash/car_wash"
          });
          console.log(2);
        },
        goCosmetology() {
          uni.navigateTo({
            url: "/pagesB/cosmetology/cosmetology?service_classify_id=2&title=美容"
          });
        },
        goMaintain() {
          uni.navigateTo({
            url: "/pagesB/maintain/maintain"
          });
        },
        goService() {
          uni.navigateTo({
            url: "/pagesB/cosmetology/cosmetology?service_classify_id=4&title=维修"
          });
        }
    }
}
